import React from 'react';
import Slider from 'react-input-slider';

let isTrue = true;
let PrepareToUpdate = true;

function sleep(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
}

async function demo(array, fps, id) {
  console.log(isTrue)
  let index = 0;
  while(isTrue) {
    console.log(fps)
    if (index === array.length) index=0;
    id.current.style.background = `url(${array[index].Context})`;
    id.current.style.backgroundSize = '300px 300px';
    index++;
    await sleep(1000/fps)
  }
}

export default class FrameViewer extends React.Component {
  constructor(props) {
    super(props);
    
    this.PreviewUpdate = React.createRef();
    this.state = {
      CurrentValue: this.props.currentFPS,
      background: this.props.background
    }
  }

componentDidMount() {
  console.log('mount');
  demo(this.state.background, this.props.currentFPS, this.PreviewUpdate);

}

componentWillReceiveProps() {
  console.log('receive');
  isTrue = false;

}

componentDidUpdate() {
  console.log(PrepareToUpdate)
  async function call(props, update) {
    console.log(props, update)
    PrepareToUpdate = false;
    await sleep(1000);
    isTrue = true;
    PrepareToUpdate = true;
    console.log(props.currentFPS);
    demo(props.background, props.currentFPS, update);
  }
  
  if (PrepareToUpdate) call(this.props, this.PreviewUpdate);
}

render() {
  return (
    <div className='viewer-container'>
      <div className='viewer' ref={this.PreviewUpdate}></div>
      <p className='fps-counter'>{this.state.CurrentValue + " FPS"}</p>
      <div className='range-slider'>  
        <Slider
          axis='x'
          xstep={1}
          xmin={1} 
          xmax={30}
          x={this.state.CurrentValue}
          onChange={({x}) => {
            this.setState({CurrentValue: x})
            this.props.getCount(x);
            } 
          }
        />
      </div>
    </div>
  );
}

}
