import React from 'react';

function LoginWindow(props) {

    return (
      <div className={'login-container '+ props.display}>
        <div className="login-window">
          <p>Enter your name</p>
          <input type="text" id="name-container" onChange={props.enterLogin}></input>
          <button className="submit-name" onClick={props.onPress}>Submit</button>
        </div>
      </div>
    )
  }

export default LoginWindow;