import React from 'react';

export default class Layers extends React.Component {

  constructor(props) {
    super(props)

    this.getContext = React.createRef();
  }

  componentDidMount() {
    this.getContext.current.style.background = `url(${this.props.context})`;
    this.getContext.current.style.backgroundSize = '100px 100px';
  }

  componentDidUpdate() {
      this.getContext.current.style.background = `url(${this.props.context})`;
      this.getContext.current.style.backgroundSize = '100px 100px';
    }

  render() {
    return (
      <div>
        <div className={'canvas-container '+this.props.display} onClick={() => {this.props.onPress(this.props.index)} }>
          <div className='layer-number'>{this.props.index}</div>
          <div className='button-container' ref={this.getContext}>
            <button className='delete' onClick={(event) => {
              event.stopPropagation();
              this.props.delete(this.props.index)}}></button>
            <button className='copy' onClick={() => { this.props.copy( this.props.index, {Context: this.props.context, Status: this.props.display, CanvasStatus: 'active-canvas'} )}}></button>
            <button className='swap' onClick={(event) => {
              event.stopPropagation();
              this.props.swap(this.props.index)}}></button>
          </div>
        </div>
      </div>
    );
  }
}